        /* 画像リスト用のスタイル */
        .container {
            width: 80vw;
            max-width: 1000px;
            margin: 0 auto;
        }

        .webgl-canvas {
            position: fixed;
            top: 0;
            left: 0;
            width: 100vw;
            height: 100vh;
            z-index: -1;
        }

        .webgl-canvas__body {
            width: 100%;
            height: 100%;
        }

        .image-list {
            width: 800px;
            margin: 0 auto;
            padding: 180px 0;
        }

        .image-item {
            width: 100%;
        }

        .image-item:not(:first-of-type) {
            margin-top: 180px;
        }

        .image-wrapper {
            display: block;
            width: 100%;
            height: 500px;
        }

        .image-wrapper>img {
            height: 100%;
            object-fit: cover;
            opacity: 0;
        }

        body {
            overscroll-behavior: none;
        }

        .wrapper {
            width: 100%;
            height: 100vh;
            position: fixed;
            top: 0;
            left: 0;
        }

        .scrollable {
            position: absolute;
            width: 100%;
            top: 0;
            left: 0;
        }